import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { filter, get, isEmpty } from "lodash";
import { Link } from "react-router-dom";

import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import MUILink from "@mui/material/Link";
import CircularProgress from "@mui/material/CircularProgress";
import CircleIcon from "@mui/icons-material/Circle";

import createMarkup from "../../../components/hooks/createMarkup";
import translate from "../../../components/hooks/translate";

import { getCountryPolicies } from "../../../store/slice/common";
import Page from "../../../components/common/Page";

const TermsAndConditions = () => {
  const dispatch = useDispatch();
  const [APIHasData, setAPIHasData] = useState(true);

  const isLoading = useSelector((state) => state.common.loading);
  const countryPoliciesData = JSON.parse(
    localStorage.getItem("system_1_country_policies")
  );

  const termsAndConditionsData = filter(countryPoliciesData, {
    policy: "TERMS_AND_CONDITIONS",
  })?.[0];

  useEffect(() => {
    if (isEmpty(countryPoliciesData) && APIHasData) {
      dispatch(getCountryPolicies("62cbc02aa8ae0099a06cd197"))
        .unwrap()
        .then((result) => {
          if (get(result, "status", "")) {
            setAPIHasData(!isEmpty(get(result, "data", [])));
          } else {
            setAPIHasData(!isEmpty(get(result, "data", [])));
          }
        })
        .catch((err) => {
          throw err;
        });
    }
  }, [APIHasData, countryPoliciesData, dispatch]);

  return (
    <>
      <Page title="Nutra Plus | Terms & Conditions" className="w-100">
        {isLoading && isEmpty(countryPoliciesData) ? (
          <Box className="w-100 h-100 align-self-center text-center">
            <CircularProgress />
          </Box>
        ) : !APIHasData ? (
          <Box className="w-100 h-100 align-self-center text-center">
            <Typography variant="body1">No data found!</Typography>
          </Box>
        ) : (
          <Box className="w-100 px-3 mt-3 mb-5">
            <Container maxWidth="lg">
              <Grid container spacing={3} alignItems="center">
                <Grid item xs={12}>
                  <Box className="text-center">
                    <Typography variant="h3" color="secondary.dark">
                      {translate(
                        get(termsAndConditionsData, "policyMacro", "")
                      )}
                    </Typography>
                  </Box>
                </Grid>
                <Grid item xs={12}>
                  <Box>
                    <Box
                      dangerouslySetInnerHTML={createMarkup(
                        translate(
                          get(termsAndConditionsData, "descriptionMacro", "NA")
                        )
                      )}
                    />
                  </Box>
                </Grid>
                <Grid item xs={12}>
                  <Box component="div" className="mt-5">
                    <Typography variant="subtitle1">
                      {translate("common_other_policies")}
                    </Typography>
                    <List>
                      <ListItem>
                        <ListItemIcon>
                          <CircleIcon sx={{ fontSize: "0.5rem" }} />
                        </ListItemIcon>
                        <MUILink
                          to="/data-policy"
                          component={Link}
                          underline="hover"
                        >
                          {translate("common_data_policy")}
                        </MUILink>
                      </ListItem>
                      <ListItem>
                        <ListItemIcon>
                          <CircleIcon sx={{ fontSize: "0.5rem" }} />
                        </ListItemIcon>
                        <MUILink
                          to="/privacy-policy"
                          component={Link}
                          underline="hover"
                        >
                          {translate("common_privacy_policy")}
                        </MUILink>
                      </ListItem>
                      <ListItem>
                        <ListItemIcon>
                          <CircleIcon sx={{ fontSize: "0.5rem" }} />
                        </ListItemIcon>
                        <MUILink
                          to="/appointment-terms-and-conditions"
                          component={Link}
                          underline="hover"
                        >
                          {translate("common_appointment_terms_and_conditions")}
                        </MUILink>
                      </ListItem>
                    </List>
                  </Box>
                </Grid>
              </Grid>
            </Container>
          </Box>
        )}
      </Page>
    </>
  );
};

export default TermsAndConditions;
